import React, { useState, useEffect } from 'react'
import styles from './Collection.module.css'
import NavigationBar from '../Navbar/NavigationBar'
import { useParams, Link } from "react-router-dom"
import { Dropdown } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { useHistory } from "react-router-dom";
import Footer from '../Footer/Footer'


export default function Collection() {

    const { id } = useParams()
    const history = useHistory()

    const [collectionArray, setCollectionArray] = useState([])
    const [singleAsset, setSingleAsset] = useState([])
    const [collectionStats, setCollectionStats] = useState('')
    const [pageNumber, setPageNumber] = useState(50)
    let [orderBy, setOrderBy] = useState('asc')
    let [sales, setSales] = useState('sale_price')
    let [filteredApi, setFilteredApi] = useState(false)
    let [input, setInput] = useState('')


    const tokenAddress = id

    let api = ` https://kaiscorsproxy.herokuapp.com/https://api.opensea.io/api/v1/assets?asset_contract_addresses=${tokenAddress}&offset=0&limit=${pageNumber}`
    const api2 = ` https://kaiscorsproxy.herokuapp.com/https://api.opensea.io/api/v1/asset_contract/${tokenAddress}`
    let filterApi = ` https://kaiscorsproxy.herokuapp.com/https://api.opensea.io/api/v1/assets?asset_contract_addresses=${tokenAddress}&order_by=${sales}&order_direction=${orderBy}&offset=0&limit=${pageNumber}`

    const options = {
        method: 'GET',
        headers: { Accept: 'application/json', 'X-API-KEY': '087158b0d16f4e73a708168cefa9123c' }
    };
    let mainApi

    // sets a filtered to the api being fetched if user chooses filter options listed on 'filter' varaible 
    if (filteredApi === false) {
        mainApi = api
    } else {
        mainApi = filterApi
    }

    const fetchData = async () => {
        const data = await fetch(mainApi, options)
        const response = await data.json()
        setCollectionArray(response.assets)
    }

    const fetchSingle = async () => {
        let data = await fetch(api2, options)
        const response = await data.json()
        setSingleAsset(response.collection)
    }


    // updates on pageNumber change, filter buttons, and filtered Api conditional
    useEffect(() => {
        fetchData()
        fetchSingle()
    }, [pageNumber, orderBy, sales, filteredApi, setCollectionArray, setSingleAsset])



    let { slug } = singleAsset

    // sets api3 based on the slug 
    const api3 = `https://api.opensea.io/api/v1/collection/${slug}/stats`

    const fetchCollectionStats = async () => {
        const response = await fetch(api3, options)
        const data = await response.json()
        setCollectionStats(data.stats)
    }

    useEffect(() => {
        fetchCollectionStats()
    }, [api3])

    function increaseNum() {
        setPageNumber(pageNumber + 50)
    }

    // grey image is used if an image is not found 
    let grey_image = '/nft-spotter/imgs/grey.png'
    const { floor_price, num_owners, count, seven_day_volume, one_day_sales, average_price, seven_day_sales, one_day_volume } = collectionStats && collectionStats
    const { banner_image_url, image_url, name, safelist_request_status, description } = singleAsset


    // adds a . to the proper place using regex 
    const addCommas = (number) => {
        return number !== null ? String(number).match(/^-?\d+(?:\.\d{0,2})?/) : 'undefined'
    }

    const sevDayVol = addCommas(seven_day_volume)
    const oneDayVol = addCommas(one_day_volume)
    const avgPrice = addCommas(average_price)
    const floorPrice = floor_price !== null ? String(floor_price).match(/^-?\d+(?:\.\d{0,4})?/) : 'undefined'


    // displays top display (banner, collection image, name and description)
    let topDisplay =
        (
            <div className={styles.mainWrapper}>
                <div className={styles.bannerDiv}>
                    <img src={banner_image_url !== null ? banner_image_url : grey_image} className={styles.bannerImage} alt="banner" />
                </div>
                <div className={styles.collectionDiv}>
                    <div style={{ position: 'relative' }}>
                        <img src={image_url} className={styles.collectionImage} alt="" />
                        <img src="/nft-spotter/imgs/verified-user.png" alt=""
                            style={{ display: safelist_request_status === "verified" ? 'block' : 'none', width: '35px', position: 'absolute', bottom: 0, right: 0 }} />
                    </div>

                    <h2 style={{ fontWeight: 'bold', margin: '10px', fontSize: '2.5rem' }}>{name}</h2>

                    <p style={{ width: '60%', padding: '10px', textAlign: 'center' }}>{description}</p>
                </div>

            </div>
        )

    const biggerText = { fontSize: '1.5rem', fontWeight: 'bold' }
    const ethSymbol = { width: '25px', height: '25px' }


    // displays prices under the top display 
    let priceDisplay =
        (
            <div className={styles.wrapper}>
                <div className={styles.flexWrapper}>
                    <div className={styles.statsDiv}>
                        <div className={styles.statsBox}>
                            <div>Items:</div>
                            <div style={biggerText}>{count}</div>
                        </div>
                        <div className={styles.statsBox}>
                            <div>Owners:</div>
                            <div style={biggerText}>{num_owners}</div>
                        </div>
                    </div>
                    <div className={styles.statsDiv}>
                        <div className={styles.statsBox}>
                            <div>Floor Price:</div>
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                                <div style={biggerText}>{floorPrice}</div>
                            </div>
                        </div>
                        <div className={styles.statsBox}>
                            <div>Average Price</div>
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                                <div style={biggerText}>{avgPrice}</div>
                            </div>
                        </div>
                    </div>
                </div>


                <div className={styles.flexWrapper}>
                    <div className={styles.statsDiv}>
                        <div className={styles.statsBox}>
                            <div>1d Volume</div>
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                                <div style={biggerText}>{oneDayVol}</div>
                            </div>
                        </div>
                        <div className={styles.statsBox}>
                            <div>7d Volume:</div>
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                                <div style={biggerText}>{sevDayVol}</div>
                            </div>
                        </div>
                    </div>
                    <div className={styles.statsDiv}>
                        <div className={styles.statsBox}>
                            <div>Sales Today:</div>
                            <div style={biggerText}>{one_day_sales}</div>
                        </div>
                        <div className={styles.statsBox}>
                            <div>7d Sales:</div>
                            <div style={biggerText}>{seven_day_sales}</div>
                        </div>
                    </div>
                </div>
            </div>


        )


    // functions for filters 
    const handleOrder = (order) => {
        setOrderBy(order)
        setFilteredApi(true)
    }

    const handleFilter = (filter) => {
        setPageNumber(50)
        setSales(filter)
        setFilteredApi(true)
    }

    const removeFilters = () => {
        setFilteredApi(false)
    }

    // functions for setting search token input 
    const handleChange = (e) => {
        setInput(e.target.value)
    }
    const handleSubmit = () => {
        history.push(`/assets/${tokenAddress}/${input}`)
    }

    const active = { fontWeight: 'bold' }


    // filter buttons from bootstrap 
    let filter = (
        <div className="d-flex justify-content-center align-items-center flex-column my-3">
            <div className={styles.filterWrapper}>
                <h2 className='mx-3'>Filter</h2>
                <form className={`d-flex ${styles.form}`}>
                    <div className="input-group mb-3">
                        <input onChange={handleChange}
                            type="text"
                            className="form-control"
                            placeholder="Search Token" />
                        <button
                            onClick={handleSubmit}
                            className="btn btn-secondary"
                            type="button"
                            id="button-addon2">
                            <FontAwesomeIcon icon={faSearch} />
                        </button>
                    </div>
                </form >
            </div>

            <div className={styles.filterWrapper}>
                <Dropdown style={{ margin: '0 2px' }}>
                    <Dropdown.Toggle
                        className={styles.btnPrimary}
                        id="dropdown-basic"
                        style={{ backgroundColor: "#7487c2", border: "1px solid #7487c2" }}
                    >
                        Order by
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                        <Dropdown.Item
                            style={orderBy === 'asc' && filteredApi === true ? active : null}
                            onClick={() => handleOrder('asc')}>
                            Ascending
                        </Dropdown.Item>
                        <Dropdown.Item styles={orderBy === 'desc' && filteredApi === true ? active : null}
                            onClick={() => handleOrder('desc')}>
                            Descending
                        </Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>

                <Dropdown style={{ margin: '0 2px' }}>
                    <Dropdown.Toggle
                        className={styles.btnPrimary}
                        id="dropdown-basic"
                        style={{ backgroundColor: "#7487c2", border: "1px solid #7487c2" }}
                    >
                        Sales
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                        <Dropdown.Item style={sales === 'sale_price' && filteredApi === true ? active : null}
                            onClick={() => handleFilter('sale_price')}>
                            Highest Price Transactions
                        </Dropdown.Item>

                        <Dropdown.Item
                            styles={sales === 'sale_count' && filteredApi === true ? active : null}
                            onClick={() => handleFilter('sale_count')}
                        >
                            Recently Sold
                        </Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>

                <button onClick={removeFilters} className='btn-danger btn mx-1' styles={{ height: '38px' }}>Remove Filters</button>

            </div>
        </div >
    )

    // maps out all cards 
    let nftDisplay = collectionArray && collectionArray.map(item => {
        let { id, image_url, token_id } = item
        return (
            <Link to={`/assets/${tokenAddress}/${token_id}`} key={id}>
                <div key={id} className={`card ${styles.colCard}`}>
                    <img src={image_url === '' ? grey_image : image_url} className={`card-img-top ${styles.image}`} alt="" />
                    <div className="card-body" styles={{ height: 'contain', width: '100%' }}>
                        <div className={styles.name}>{name}</div>
                        <div className="tokenId"># {token_id}</div>
                    </div>
                </div>
            </Link>
        )
    })

    return (
        <>
            <NavigationBar />

            {topDisplay}

            {priceDisplay}

            {filter}

            <div className="container-fluid">
                <div className={styles.cardDisplay}>
                    {nftDisplay}
                </div>
            </div>



            <div className="d-flex justify-content-center my-3">
                <button onClick={increaseNum}

                    className={`btn btn-secondary ${styles.bottomButton}`}
                    type="button">
                    Load More
                </button>
            </div>
            <Footer />
        </>
    )
}
